
html {
  line-height: 1.4;
  font-size: 18px;
  color: #2c3e50;
}
body {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: normal;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
p {
  margin: 0;
}
a {
  text-decoration: none;
  color: blackColor;
}
audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}
section {
  position: relative;
  padding: 24px 0;
}
textarea {
  resize: none;
}
input,
select,
button {
  outline: none;
}
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.wrapper {
  height: 100vh;
  z-index: 1;
}
.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 60px;
  position: relative;
}
.button-main {
  padding: 8px 12px;
  margin-right: 8px;
  font-size: 16px;
  color: #fff;
  background-color: #ccc;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  -webkit-transition: all 0.25s cubic-bezier(0.02, 0.01, 0.47, 1);
  transition: all 0.25s cubic-bezier(0.02, 0.01, 0.47, 1);
}
.button-main.active {
  background-color: #446cb3;
}
.button-main.button--second {
  padding: 0;
  margin-right: 0;
  color: #333;
  background-color: transparent;
  border-radius: 0;
  border-bottom: 1px solid #333;
}
.button-main.button--success {
  background-color: #008000;
}
.button-main:hover {
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
  -webkit-transition-delay: 0s !important;
          transition-delay: 0s !important;
}
.delete-button {
  position: absolute;
  top: 8px;
  right: 0;
  width: 18px;
  height: 18px;
  cursor: pointer;
  border-radius: 50%;
}
.delete-button:before,
.delete-button:after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  width: 18px;
  height: 1px;
  background-color: #333;
}
.delete-button:before:hover,
.delete-button:after:hover {
  background-color: #e62f57;
}
.delete-button:before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.delete-button:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.task-input {
  width: 100%;
  margin-bottom: 20px;
  padding: 8px 16px;
  font-size: 18px;
  border-radius: 8px;
  border: 1px solid #ccc;
}
.task-input:focus {
  outline: 0;
}
.todo-list {
  padding-bottom: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid #ccc;
}
.todo-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
  margin-bottom: 6px;
}
.todo-item:last-child {
  margin-bottom: 0;
}
.todo-item-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.todo-item-title {
  padding: 6px;
  font-size: 20px;
}
.todo-item-title.todo-item-title--edit {
  border: 1px solid #ccc;
}
.todo-item-title.completed {
  text-decoration: line-through;
  color: #ccc;
}
.todo-info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-bottom: 20px;
  margin-bottom: 20px;
  border-bottom: 1px solid #ccc;
}
.todo-menu,
.todo-menu-check {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}